import axios from 'axios';

export const LIST_COUNTRIES ='LIST_COUNTRIES';
export const COUNTRY_DETAIL = 'COUNTRY_DETAIL';
export const ADD_ACTIVITY = 'ADD_ACTIVITY';


export function pageCountries (continent, season, order){
    let queryUrl = `localhost:3001/countries?`
    if(continent) queryUrl = queryUrl + `continent=${continent}`;
    if(season) queryUrl = queryUrl + `touristSeason=${season}`;
    if(order) queryUrl = queryUrl + `order=${order}`;
    return async function(dispatch){
        await axios.get(queryUrl)
        .then(response => dispatch({
            type: LIST_COUNTRIES,
            payload: response
        }))
    }
}

export function getCountryDetail (id){
    return async function(dispatch){
        await axios.get(`localhost:3001/countries/${id}`)
        .then(response => dispatch({
            type: COUNTRY_DETAIL,
            payload: response
        }))
    }
}

export function addActivity (name, difficulty, duration, season, codeCountry){
    return async function(dispatch){
        await axios.post('localhost:3001/activities', {
            name,
            difficulty,
            duration,
            season,
            codeCountry,
        })
        .then(response => dispatch({
            type: ADD_ACTIVITY,
            payload: response
        }))
    }
}