import { LIST_COUNTRIES, COUNTRY_DETAIL, ADD_ACTIVITY } from "../actions/actions"

const initialState = {
    countriesLoaded: [],
    activitiesLoaded: [],
    countryDetail: {}
}

export default function rootReducer(state = initialState, {type, payload}) {
    switch(type){
        case LIST_COUNTRIES:
            return {...state, countriesLoaded: [...state.countriesLoaded, payload]}
        
        case COUNTRY_DETAIL:
            return {...state, countryDetail: [payload]}

        case ADD_ACTIVITY:
            return {...state, activitiesLoaded: [...state.activitiesLoaded, payload]}

        default:
            return state
    }
}